@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100&display=swap);
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  scroll-behavior: smooth;
}
body{
  background-color: rgb(53, 51, 51);
  color: white;
  font-family: 'poppins','sans-serif';
}

.trending{
  justify-content:center;
  margin: 20px 45%;
}
@media only screen and (max-width: 600px) {
  .logo img{
    height:30px;
    width:30px;
    margin: 0 5px;
  }
  .logo h3{
    font-size: 10px;
  }
  .trending{
    justify-content:center;
    margin: 20px 35%;
  }
  .search{
    background-color:rgb(17, 17, 17);
    margin: 0 5px;
    border: 2px solid rgb(36, 65, 228);
    border-radius:20px;
    padding:3px;
    
    color: white;
    font-family: 'poppins','sans-serif';
  
  }
}
.movie-info{
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color:rgb(17, 17, 17);
  
  padding:1rem;
}
.movie-info h3{
  font-size:1rem;
}
.movie-container{
  display: flex;
  flex-direction: row;
  justify-content:space-around;
  flex-wrap: wrap;
  justify-items: center;
  max-height: 100vh;
  align-items: center;
}
.movie{
  width: 250px;
  margin:1rem;
  box-shadow: 0 2px 4px rgba(0,0,0,1.5);
  border-radius:3px;
  position: relative;
  overflow: hidden;
}
.movie img{
  object-fit:cover;
  width:100%;
}

header{
  background-color:rgb(17, 17, 17);
  color: white;
  padding: 1rem;
  display: flex;
  justify-content:space-between;
  
}

.logo-img{
  height:50px;
  width:50px;
  margin: 0 5px;
  z-index: 100;
}
.logo{
  display:flex;
  background-color: rgba(0,0,0,0.3);
  z-index: 100;
  align-items: center;
  padding: 15px 0;
  width: 100%;
  cursor: pointer;
  position: fixed;;
  justify-content:center;
}
.logo h3{
  margin-left:10px;
  z-index: 100;
}
.search{
  background-color:rgb(17, 17, 17);
  margin: 0 5px;
  border: 2px solid rgb(36, 65, 228);
  border-radius:20px;
  padding:3px;
  text-align: center;
  color: white;
  font-family: 'poppins','sans-serif';

}


.search:focus{
  outline: none;
}

.tag{
    background:rgb(36, 34, 34);
    padding:0.5rem;
    font-weight: bold;
}

.red{
  color: red;
}
.orange{
  color: rgb(240, 169, 37);
}
.green{
  color: rgb(10, 173, 10);
}

.movie-over{
  position: absolute;
  border-top-left-radius:3px;
  border-top-right-radius:3px;
  background-color: rgb(36, 65, 228);
  color: rgb(211, 181, 50);
  bottom:0;
  left:0;
  right: 0;
  padding: 1rem;
  overflow:auto;
  max-height: 80%;
  -webkit-transform: translateY(102%);
          transform: translateY(102%);
}
.movie-over p{
  font-size: 14px;
}
.movie-over h2{
  font-size: 18px;
}
.movie:hover .movie-over{
  -webkit-transform: translateY(0%);
          transform: translateY(0%);
  transition: -webkit-transform .3s ease-in-out;
  transition: transform .3s ease-in-out;
  transition: transform .3s ease-in-out, -webkit-transform .3s ease-in-out;
}

.media{
    display: flex;
    flex-direction:column;
    width:250px;
    padding: 5px;
    margin: 15px 0;
    background:rgb(17, 17, 17);
    border-radius:10px;
    position: relative;
    font-family: 'poppins','sans-serif';
    color: #f4f4f4;
}

@media (max-width:550px){
    .media{
        width : 50%;
        justify-content: center;
    }
    .subtitle{
        font-size:11px;
    }
}

.media:hover{
    background:rgb(224, 224, 224);
    color:#000;
}
.poster{
    border-radius: 10px;
}
.title{
    text-align:center;
    font-size:18px;
    padding:5px 0;
    width:100%;
}
.subtitle{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding:5px 5px;
    
}
.ContentModal__landscape {
  object-fit: contain;
  border-radius: 10px;
}

.ContentModal__portrait {
  display: none;
  object-fit: contain;
  border-radius: 10px;
}

.tagline {
  padding-bottom: 10px;
  align-self: center;
  margin: 2px 0;
}

.ContentModal {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
  width: 100%;
  overflow-y: scroll;
  scrollbar-width: none;
}

.ContentModal::-webkit-scrollbar {
  display: none;
}

.ContentModal__about {
  padding: 10px;
  width: 95%;
  height: 90%;
  display: flex;
  flex-direction: column;
  font-family: "Roboto", sans-serif;
  justify-content: space-evenly;
  align-items: center;
  font-weight: 300;
}

.ContentModal__title {
  height: 12%;
  font-size: 5vw;
  display: flex;
  align-items: center;
  justify-content: center;
}

.ContentModal__description {
  display: flex;
  height: 40%;
  overflow-y: scroll;
  margin: 5px 0;
  padding: 15px;
  border-radius: 20px;
  scrollbar-width: thin; /* Firefox */
  box-shadow: inset 0 0 5px #000000;
  text-align: justify;
}

.ContentModal__description::-webkit-scrollbar {
  display: none;
}

@media (min-width: 835px) {
  .ContentModal__landscape {
    display: none;
  }
  .ContentModal__portrait {
    display: flex;
    width: 35%;
    margin-right: 20px;
  }
  .ContentModal {
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    padding: 10px 0;
  }
  .ContentModal__about {
    width: 58%;
    padding: 0;
    height: 100%;
  }
  .ContentModal__title {
    font-size: 2vw;
  }
  .ContentModal__description {
    font-size: 22px;
  }
}
.Trending{
    
    display: flex;
    flex-wrap: wrap;
    margin-bottom: 50px;
    justify-content:space-around;
    align-items:center;
}
.pagetitle{
    display: flex;
    text-transform: uppercase;
    justify-content:center;;
    font-size:3vw;
    padding:5px;
    margin-top: 80px;
    border-radius:50px;
    color:white;
}
@media (max-width: 1000px){
    .pagetitle{
        font-size:5vw;
    }
}
.paging{
    margin-bottom: 100px;
    width: 100%;
    display: flex;
    justify-content:center;
    margin-top:10px;
}
